/* Custom styles */
.masonry-grid {
    display  : flex;
    flex-wrap: wrap;
    gap      : 20px;
}

.swiper-container {
    width : 100%;
    height: 100%;
}

.wp-block-gallery.swiper-container {
    height: 500px;

    .swiper-slide {
        display        : flex;
        align-items    : center;
        justify-content: center;

        img {
            max-height: 100%;
            width     : auto;
            object-fit: contain;
        }
    }

    .swiper-pagination-bullet {
        background: #fff;
        opacity   : 0.5;

        &-active {
            opacity: 1;
        }
    }
}

.menu-toggle {

    &:focus,
    &:active {
        outline   : none;
        border    : none;
        box-shadow: none;
    }
}

.menu-toggle {
    width     : 30px;
    height    : 17px;
    position  : relative;
    background: transparent;
    border    : none;
    cursor    : pointer;
    padding   : 0;
    z-index   : 100;

    span {
        display   : block;
        position  : absolute;
        height    : 3px;
        width     : 100%;
        background: $primary;
        transition: all 0.3s ease;

        &:nth-child(1) {
            top             : 0;
            transform-origin: center;
        }

        &:nth-child(2) {
            top       : 50%;
            transform : translateY(-50%);
            transition: opacity 0.3s ease;
        }

        &:nth-child(3) {
            bottom          : 0;
            transform-origin: center;
        }
    }
}

.navbar-toggler[aria-expanded="true"] {
    span {
        &:nth-child(1) {
            transform: rotate(45deg);
            top      : 11px;
        }

        &:nth-child(2) {
            opacity: 0;
        }

        &:nth-child(3) {
            transform: rotate(-45deg);
            bottom   : 11px;
        }
    }
}

.main-navigation {
    @media (max-width: 768px) {
        #primary-menu {
            display   : none;
            position  : absolute;
            top       : 60px;
            left      : 0;
            width     : 100%;
            background: #fff;
            padding   : 20px;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

            &.show {
                display: block;
            }
        }
    }
}

.navbar-nav {
    width  : 100%;
    padding: 1rem;

    @media (max-width: 991px) {
        background: #fff;
        position  : absolute;
        top       : 100%;
        left      : 0;
        right     : 0;
    }
}

.nav-item {
    margin: 0.5rem 0;
}



