@mixin letitre {
    font-family   : "TextaPro-Heavy";
    margin        : 43px 35px 0px 35px;
    font-size     : 32.6px;
    line-height   : 27px;
    font-weight   : 900;
    font-stretch  : normal;
    font-style    : normal;
    line-height   : .86;
    letter-spacing: normal;
    text-align    : center;
    color         : #fff;

    strong {
        color: #ec6c23
    }
}

@mixin maxWidth ($margin) {
    max-width: 86%;
    margin   : $margin auto $margin auto;
}

@mixin boissonTitre($color) {
    text-transform: uppercase;
    font-family   : "Gilroy-Heavy";
    font-size     : 30px;
    font-stretch  : normal;
    font-style    : normal;
    line-height   : 1;
    letter-spacing: normal;
    text-align    : left;
    color         : $color;

}

@mixin boissonTexte($color) {

    text-align    : left;
    font-family   : "Gotham-Medium";
    font-size     : 10px;
    letter-spacing: 0;
    color         : $color;
}

