#klm {
    width    : 100%;
    max-width: 1200px;
    margin   : 0 auto;

    figure {
        width  : calc(33.333% - 10px);
        margin : 0;
        padding: 5px;

        img {
            width     : 100%;
            height    : auto;
            display   : block;
            cursor    : pointer;
            transition: opacity 0.3s;

            &:hover {
                opacity: 0.8;
            }
        }
    }

    @media (max-width: 768px) {
        figure {
            width: calc(50% - 10px);
        }
    }

    @media (max-width: 480px) {
        figure {
            width: 100%;
        }
    }
}